.page-content {
  display: flex;
  flex-wrap: wrap;
}

.page-content {
  .main-content {
    flex: 2;
    order: 1;

  }
}

.page-content .sidebar.first {
  flex: 1;
  background-color: #e5f5fe;
  padding-right: 2em;
}

.etree-content {
  display: flex;
  flex-wrap: wrap;
}

.etree-content .etree-table {
  flex: 2;
  padding-right: 1em;
}

.etree-content .etree-sidebar {
  flex: 1;
  background-color: #e5f5fe;
  padding-left: 1em;
}

.menu-horizontal {
  display: flex;
  flex-wrap: wrap;
}

.menu-horizontal li {
  padding: 5px 10px;
}

form .form-wrapper details .details-wrapper {
  background-color: #ffffdf;
}

.field-multiple-table .field-multiple-table tr {
  background-color: #ddeeff;
}

.entity-tech-party-settings-edit-form th {
  display: none;
}

.entity-tech-party-settings-edit-form .tabledrag-toggle-weight-wrapper {
  display: none;
}

.entity-tech-party-settings-edit-form td.field-multiple-drag {
  display: none;
}

.entity-tech-party-settings-edit-form td {
  display: flex;
}

.entity-tech-party-settings-edit-form .js-form-type-url {
  padding-right: 1em;
}

.entity-tech-party-settings-edit-form summary {
  background-color: #5599ff;
  color: #fff;
}

.toolbar-menu > li > a {
  padding: 8px 5px
}

.menu-block a.is-active {
  color: #f95;
  background-color: #fefefe;
  padding: 2px 4px
}

.menu-block h3 {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}

.menu-block .item-list ul {
  margin: .05em 0 .05em .5em;
}